@import '../core/variables.scss';

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Roboto', sans-serif;
  list-style: none;
}

a {
  text-decoration: none;
  color: $color-rrss !important;
}

a:focus,
a:hover {
  opacity: 0.6 !important;
  transform: scale(1.5) !important;
}
